.input {
  position: relative;

  display: flex;
  align-items: center;

  color: $label-color;
  text-transform: uppercase;
}

.input__text {
  width: 100%;
  margin-left: 15px;
  padding: 13px 13px 14px;

  font: inherit;
  color: $main-text-color;
  text-transform: uppercase;

  border: 1px solid $input-color;

  &:hover {
    outline: 1px solid $input-color--hover;
  }

  &:focus,
  &:active {
    outline: 2px solid $input-color--active;
  }

  &:active:invalid {
    outline: 2px solid $input-color--error;
  }

  @media (min-width: $tablet-width) {
    min-height: 52px;
    padding: 11px 20px;
  }
}

.input__text:focus:invalid + .input__icon {
  fill: $input-color--error;
}

.input__text:focus + .input__icon {
  fill: $special-color;
}

.input__icon {
  position: absolute;
  top: 50%;
  right: 24px;
  z-index: 5;

  transform: translateY(-50%);
}

.input__icon--email {
  width: 18px;
  height: 14px;
}

.input__icon--phone {
  width: 21px;
  height: 21px;
}

.input__text--area {
  height: 150px;
  margin: 0;
  padding: 18px 16px;
  overflow: auto;

  resize: none;

  @media (min-width: $tablet-width) {
    height: 175px;
  }

  @media (min-width: $desktop-width) {
    padding: 15px 18px;
  }
}
